<template>
    <div>
        <div>
            <input type="text" v-model="input" @keyup="keyHandel">
            <p>keyup 입력 결과 : {{ result1 }}</p>
        </div>

        <div>
            <input type="text" v-model="input2" @keydown="keyHandel2">
            <p>keydown  결과 : {{ result2 }}</p>
        </div>

          <div>
            <input type="text" v-model="input3" @keyup.enter="keyHandel3">
            <p>keydown  결과 : {{ result3 }}</p>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            result1: '',
            input:'',
            result2: '',
            input2:'',
            result3: '',
            input3:''
        }
    },
    methods : {
        keyHandel1(){
            this.result1 = this.input;
        },
        
        keyHandel2(){
            this.result2 = this.input2;
        },

        keyHandel3(){
            this.result3 = this.input3;
        }
    }
};
</script>
