<template>
    <div>
        <!-- 클릭이벤트 추가, 클릭 발생했을 때 increaseCounter 함수 호출-->
        <div>
            <button type="button" @click="increaseCounter">Add 1</button>
            <p>The counter is : {{ counter }}</p>
        </div>

        <div>
            <button type="button" @click="setCount(7)">Set 7</button>
            <p>The counter is : {{ counter2 }}</p>
        </div>

        <div>
            <button type="button" @click="one(), two()">Click</button>
        </div>
        
    </div>
</template>

<script>

export default {
    data() {
        return{
            counter : 0
        };
    },
    methods : {
        increaseCounter(){
            this.counter = this.counter + 1; //counter를 1씩 증가
        },
  
    
        setCount(counter2){
            this.counter2 = counter2;
        },

        one(){
            alert('One');
        },

        two(){
            alert('Two');
        }
    }
    
};
</script>
