<template>
    <div>
        <table>
            <thead> 
                <tr>
                    <th>제품명</th>
                    <th>가격</th>
                    <th>카테고리</th>
                    <th>배송료</th>
                </tr>
            </thead>

            <tbody>
                <tr :key="i" v-for="(product, i) in productList"> <!-- v-for="(item, index) in items" item은 데이터 배열 -->
                    <td>{{product.product_name}}</td>
                    <td>{{product.price}}</td>
                    <td>{{product.category}}</td>
                    <td>{{product.delivery_price}}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
 
<script>

export default {
    data() {
        return {
            productList:[]
        };
    },
    created(){
        this.getList();
    },
    methods: {
        async getList(){
            this.productList = await this.$api("https://a72338cc-de60-4632-add8-eaa29c8317f3.mock.pstmn.io/test","get");
        }
    }
}
</script>

