<template>
  <div>
    <div>
      <h1>Hello, {{title}}</h1>
    </div>

     <div>
            <p class="ptext">select </p>
            <select v-model="city">
                <option value="02">서울</option>
                <option value="21">부산</option>
                <option value="064">제주</option>
            </select>
        </div>

        <div>
            <p class="ptext"> checkbox </p>
            <label> <input type="checkbox" v-model="checked">{{ checked }}</label>
        </div>

        <div>
            <label> <input type="checkbox" v-model="checked2" true-value="yes" false-value="no">{{ checked2 }}</label>
        </div>

        <div>
            <label><input type="checkbox" value="서울" v-model="check_city">서울</label>
            <label><input type="checkbox" value="부산" v-model="check_city">부산</label>
            <label><input type="checkbox" value="제주" v-model="check_city">제주</label>
            <br>
            <span>체크한 지역 : {{ check_city }}</span>
            <br>
        </div>

        <div>
            <p class="ptext"> radio </p>
            <label><input type="radio" v-bind:value="radioValue1" v-model="picked">서울</label>
            <label><input type="radio" v-bind:value="radioValue2" v-model="picked">부산</label>
            <label><input type="radio" v-bind:value="radioValue3" v-model="picked">제주</label>
            <br>
            <span> 선택한 지역 : {{ picked }}</span>
        </div>

        <div>
            <p class="ptext"> Img 객체 src </p>
            <img v-bind:src="imgSrc"/>
        </div>

        <div>
           <p class="ptext"> button 객체의 disabled </p>
          <input type="text" v-model="textValue" />
          <button type="button" v-bind:disabled="textValue==''">Click</button>
        </div>

        <div>
           <p class="ptext"> 클래스바인딩 </p> <!-- 클래스 하나하나 만들어서 묶음-->
           <div class="container" v-bind:class="{
            'active': isActive, 'text-ted':hasError // isActive : true, hasError : false
           }">Class Binding</div>
        </div>

        <div>
           <p class="ptext"> 인라인 스타일바인딩 </p> <!-- -->
           <div class="container" v-bind:style="[baseStyle, addStyle]">인라인 스타일바인딩</div>
        </div>

        <div>
           <p class="ptext"> 리스트 렌더링 v-for </p>
           <ProductList :productList="productList" /> <!-- Props는 소문자로 시작 -->
        </div>

        <div>
            <p class="ptext"> 렌더링문법 v-if, v-show </p>
            <h1 v-if="type=='A'">A</h1>
            <h1 v-else-if="type=='B'">B</h1>
            <h1 v-else>C</h1>
            <h1 v-show="type=='A'">type=A면, 현재 블록이 화면에 보인게 됩니다</h1>
        </div>

        <div>
          <p class="ptext"> 클릭이벤트 v-on:click </p>
          <EventClick />
        </div>

        <div>
          <p class="ptext"> chage이벤트 @change="메소드명" </p>
          <EventChange />
        </div>

        <div>
            <p class="ptext"> Key이벤트 @keyup.enter="submit" </p>
            <EventKey />
        </div>

        <div>
          <p class="ptext"> computed </p>
          <FullName />
        </div>

        <div>
          <p class="ptext"> Watch </p>
          <FullNameWatch />
        </div>
  </div>
</template>

<script>
import ProductList from '../components/ProductList.vue';
import EventClick from '../components/EventClick.vue';
import EventChange from '../components/EventChange.vue';
import EventKey from '../components/EventKey.vue';
import FullName from '../components/FullName.vue';
import FullNameWatch from '../components/FullNameWatch.vue';

export default {
    data() {
        return{
            title: 'World',
            checked : true,
            checked2 : true,
            city : "064",
            check_city : [],
            picked : '',
            radioValue1 : '서울',
            radioValue2 : '부산',
            radioValue3 : '제주',
            imgSrc: "https://kr.vuejs.org/images/logo.png",
            textValue : " ",
            isActive : true,
            hasError : false,
            baseStyle : 'background-color:green; width:100%; height:200px;',
            addStyle : 'color:white; font-weight:bold;',
            type : 'A'
        };
    },
      components: {
        ProductList,
        EventClick,
        EventChange,
        EventKey,
        FullName,
        FullNameWatch
  },
};
</script>

<style scoped>
  .container {
    width : 100%;
    height : 200px;
  }

  .ptext {
    font-size: 20px;
    font-weight: bold;
    border-bottom:1px solid #048842;
    color: #048842;
  }

  .active {
    background-color: yellow;
    font-weight: bold;
  }

  .text-red {
    color : red;
  }
</style>

